<template>
    <div class="container wrapper">
        <Logo :disabled="!+subscriptionInfo.hasAccess" />
        <span class="info__title">
            Мой двор
        </span>
        <span class="info__status"
              :class="{active: +subscriptionInfo.hasAccess}"
        >
            Подписка {{+subscriptionInfo.hasAccess ? "активна" :
                !+subscriptionInfo.hasAccess && abonentInfo.hasSubscription ? "неактивна"
                : "заблокирована"}}
        </span>
        <!-- ПОДПИСКА НЕ ОПЛАЧЕНА, НО АКТИВНА - ТРИАЛ  -->
        <div v-if="subscriptionInfo.creditTimeEnd">
            <div class="info__warning">
                <span>Недостаточно средств на вашем счете</span>
                <span>{{dataFormat(subscriptionInfo.creditTimeEnd)}} подписка будет заблокирована </span>
            </div>
            <div class="info__detail">
                <div class="info__detail_row">
                    Расчетный период
                    <span class="info__detail_date">
                        {{dataFormat(subscriptionInfo.nextPayDate || subscriptionInfo.creditTimeEnd)}}
                    </span>
                </div>
                <div class="info__detail_row">
                    Требуется оплата
                    <span class="info__detail_price">
                        {{subscriptionInfo.payRecommended}} <span class="ruble">
                            &#8381;
                        </span>
                    </span>
                </div>
            </div>
        </div>
        <div v-else-if="!!+subscriptionInfo.hasAccess && subscriptionInfo.endDate">
            <div class="info__detail">
                <div class="info__detail_row">
                    Подписка отменится
                    <span class="info__detail_date">
                        {{dataFormat(subscriptionInfo.endDate)}}
                    </span>
                </div>
            </div>
        </div>
        <!-- ПОДПИСКА НЕ ОПЛАЧЕНА, ЗАБЛОКИРОВАНА - ТРИАЛ  -->
        <!-- тут не понятно нужно ли условие про баланс ибо nextpaydate, возможно это из-за тестовой базы -->
        <div v-else-if="!+subscriptionInfo.hasAccess && balance < 0">
            <div class="info__warning">
                Недостаточно средств на вашем счете
            </div>
            <div class="info__detail">
                <div class="info__detail_row">
                    Подписка будет отключена
                    <span class="info__detail_date">
                        {{dataFormat(subscriptionInfo.nextPayDate || subscriptionInfo.creditTimeEnd || subscriptionInfo.endDate)}}
                    </span>
                </div>
                <div class="info__detail_row">
                    Требуется оплата
                    <span class="info__detail_price">
                        {{subscriptionInfo.payRecommended}} <span class="ruble">
                            &#8381;
                        </span>
                    </span>
                </div>
            </div>
        </div>
        <!--  ПРОБНЫЙ ПЕРИОД ЗАКОНЧИЛСЯ, БАЛАНСА ДОСТАТОЧНО ДЛЯ ОПЛАТЫ -->
        <div v-else>
            <div class="info__detail">
                <div class="info__detail_row">
                    Расчетный период
                    <span class="info__detail_date">
                        {{dataFormat(subscriptionInfo.nextPayDate || subscriptionInfo.creditTimeEnd || subscriptionInfo.endDate)}}
                    </span>
                </div>
                <div class="info__detail_row">
                    Затем спишется
                    <span class="info__detail_price">
                        {{subscriptionInfo.payRecommended }} <span class="ruble">
                            &#8381;
                        </span>
                    </span>
                </div>
            </div>
        </div>
        <div class="info__services">
            <div class="info__services_title"
                 @click="servicesHidden = !servicesHidden"
            >
                <span class="info__services_title-text">
                    Подключенные услуги
                </span>
                <img src="../assets/images/arrow-back.svg"
                     alt="Свернуть"
                     class="info__services_title-arrow"
                     :class="{opened: !servicesHidden}"
                     width="24"
                     height="24"
                />
            </div>
            <div class="info__services_list"
                 :class="{hidden: servicesHidden}"
            >
                <div v-for="(service, index) in services"
                     :key="service"
                     class="info__service"
                >
                    <span class="service__name">
                        {{+index + 1}}.&nbsp;{{service["SERVICE_NAME"] === "Видеонаблюдение" ? "Камеры во&nbsp;дворе" : service["SERVICE_NAME"] === "Умный домофон PRO" ? "Умный домофон" : service["SERVICE_NAME"]}}
                    </span>
                    <span class="service__price">
                        {{service.PRICE[1]}} <span class="ruble">
                            &#8381;
                        </span>/мес
                    </span>
                </div>
            </div>
        </div>
    </div>
    <ModalWindow
        v-show="payModalDisplay"
        @click.self="closePayModal"
    >
        <template #title>
            <img src="../assets/images/modal_error.svg"
                 alt="error"
                 class="modal__header_img"
            />
            Не удалось списать оплату
        </template>
        <template #description>
            Недостаточно средств на вашем счёте, подписка будет
            {{!subscriptionInfo.hasAccess ? "заблокирована" : "отключена"}}&nbsp;{{dataFormat(subscriptionInfo.nextPayDate || subscriptionInfo.endDate)}}
        </template>
        <template #footer>
            <Btn
                :color="'gray'"
                :small="true"
                @click="closePayModal"
            >
                Отменить
            </Btn>
            <Btn
                :small="true"
                @click="sendEventFromPayModal(), goToMerchant()"
            >
                Пополнить
            </Btn>
        </template>
    </ModalWindow>
</template>

<script>
    import {
        defineAsyncComponent,
        defineComponent,
    } from "vue";
    import { getSawPayModalCookie } from "./Constants";

    export default defineComponent({
        name: "SubscribeInfo",
        components: {
            Logo: defineAsyncComponent(() => import("@/components/Logo.vue")),
            ModalWindow: defineAsyncComponent(() => import("@/components/ModalWindow.vue")),
            Btn: defineAsyncComponent(() => import("@/components/Button.vue")),
        },
        data() {
            return {
                servicesHidden: false,
                sawPayModalCookie: false,
                clicked: false,
            };
        },
        computed: {
            services() {
                let services = this.$store.state.formattedServices;

                Object.filter = (obj, predicate) =>
                    Object.keys(obj)
                        .filter(key => predicate(obj[key]))
                        .reduce((res, key) => (res[key] = obj[key], res), {});
                services = Object.filter(services, service => service.HAS_SERVICE === "1");

                return services;
            },
            abonentInfo() {
                return this.$store.state.abonentInfo;
            },
            payModalDisplay() {
                return this.$store.state.payModalDisplay;
            },
            subscriptionInfo() {
                return this.$store.state.subscriptionInfo;
            },
            balance() {
                return this.$store.state.balance;
            },
            payMethod() {
                return this.$store.state.payMethod;
            },
        },
        watch: {
            services(value) {
                this.servicesHidden = Object.keys(value).length > 1;
            },
        },
        async mounted() {
            await this.$store.dispatch("getSubscriptionInfo");
            this.getSawPayModalCookie();

            this.servicesHidden = Object.keys(this.services).length > 1;
        },
        methods: {
            dataFormat(date) {
                if(!date) {
                    return ""
                }
                return new Date(date).toLocaleString().slice(0, 10);
            },
            getSawPayModalCookie() {
                this.sawPayModalCookie = getSawPayModalCookie();
            },
            closePayModal() {
                this.setCookie();
                this.$store.dispatch("changePayModalDisplay", false);
                if (this.subscriptionInfo.hasAccess) {
                    this.$store.dispatch("sendUserEvent", "YardSubManagementModuActivelClose");
                } else {
                    this.$store.dispatch("sendUserEvent", "YardSubManagementModuBlockCancel");
                }
            },
            setCookie() {
                let date = this.subscriptionInfo.nextPayDate.split(".").reverse().join("-");

                date = new Date(date).toUTCString();
                document.cookie = `saw_pay_modal=true; path=/; expires=${date}`;
            },
            goToMerchant() {
                if (!this.clicked) {
                    let screen = this.payMethod === "card" ? "SubscriptionPay" : "PaymentFromLK";
                    let deepLink = `isapp://lk?screen=${screen}&sum=${this.subscriptionInfo.payRecommended}`;
                    this.deepLinkImitator(deepLink);
                }

                this.clicked = true;
                setTimeout(() => this.clicked = false, 2000);
                this.$store.dispatch("changePayModalDisplay", false);
            },
            deepLinkImitator(deepLink) {
                const link = document.createElement("a");
                link.setAttribute("href", deepLink);
                link.click();
            },
            sendEventFromPayModal() {
                if (+this.subscriptionInfo.hasAccess) {
                    this.$store.dispatch("sendUserEvent", "YardSubManagementModulActiveTopUp");
                } else {
                    this.$store.dispatch("sendUserEvent", "YardSubManagementModulBlockTopUp");
                }
            },
        },
    });
</script>

<style scoped>
.wrapper {
  position: relative;
  align-items: flex-start;
  margin-top: 24px;
  padding: 16px;
  box-sizing: border-box;
  box-shadow: 0px 2px 10px rgba(6, 16, 41, 0.1);
  border-radius: 14px;
  letter-spacing: -0.02em;
}

.modal__header_img {
  width: 50px;
  height: 50px;
  margin: 0 auto 16px;
}

.info__title {
  margin-bottom: 8px;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
}

.info__status {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  color: #9B9FA9;
  margin-bottom: 8px;
}

.info__status.active {
  color: #36B88C;
}

.info__warning {
  display: flex;
  flex-direction: column;
  margin-bottom: 8px;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.02em;
  color: #FD6B54;
}

.info__detail_row {
  display: flex;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}

.info__detail_row > span {
  margin-left: 4px;
}

.info__detail_row:not(:last-of-type) {
  margin-bottom: 2px;
}

.info__detail_date {
  font-weight: 500;
}

.info__detail_date:empty {
  color: #FFFFFF;
}

.info__detail_price {
  color: #1D86FF;
}

.info__services {
  width: 100%;
  display: flex;
  position: relative;
  flex-direction: column;
}

.info__services_list {
  display: flex;
  height: min-content;
  flex-direction: column;
  margin-top: 8px;
  visibility: visible;
  opacity: 1;
  transition-property: all;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
}

.info__services_list.hidden {
  margin-top: 0;
  height: 0px;
  visibility: hidden;
  opacity: 0;
}

.info__services_list.hidden .info__service {
  height: 0px;
  margin-bottom: 0;
  visibility: hidden;
  opacity: 0;
}

.info__services_title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
}

.info__services_title-text {
  font-weight: 600;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: #9B9FA9;
}

.info__services_title-arrow {
  transition-property: transform;
  transition-duration: .25s;
  transition-timing-function: ease-in-out;
  transform: rotate(90deg);
}

.info__services_title-arrow.opened {
  transform: rotate(-90deg);
}

.info__service {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 8px;
}

.info__service:last-of-type {
  margin-bottom: 0;
}

.service__name {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -0.02em;
  color: #061029;
}

.service__price {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #6A707F;
}
</style>